import Category from '../page/Category.vue'
import TabBar from './TabBar.vue'
export default {
    name:'index',
    components:{
        Category,
        TabBar
    },
    
    data(){
        return{
            clickIndex:0,
            btnList:['每日','每周','每月'],
            imgUrl:require("@/assets/img/Oval.png")
        }
    },
    methods:{ 
        addClass(index){ 
            this.clickIndex=index;
        }
    },
}



