import echarts from "echarts";
import {reportData} from '../../server/report'
export default {
  name: "category",
  data() {
    return {

    };
  },
  created(){
    this.$nextTick(()=> {
            this.loadEchart()
            this.percentEchart()
        })
    this.getData()
  },
  mounted() {
    // let that = this;
    //   window.onresize = function() {
    //       this.myChart.resize()
    //       this.myChartPersend.resize()
    //   }
  },
  methods: {

    getData(){
      reportData().then(e=>{
        if(e.data.code==200){
          console.log(e);
        }
      })
    },
    //柱状图汇总
    loadEchart() {
        this.myChart = echarts.init(document.getElementById("chart_example"));
        this.myChart.setOption({
      tooltip: {
        trigger: "axis"
      },
      // legend: {
      //   data: ['2011年', '2012年']
      // },
      xAxis: {
        type: "value",
        boundaryGap: [0.2, 0.2],
        max: 1400,
        min: 0,
      },
      yAxis: [
        {
          type: "category",
          data: []
        },
      ],
      series: [
        {
          name: '2011年',
          type: 'bar',
          data: [400, 300, 100, 500, 600, 700],
        },
      ]
      });
    },
    //员工占比
    percentEchart() {
        this.myChartPersend = echarts.init(document.getElementById("persendChart"));
        this.myChartPersend.setOption({
      tooltip: {
        trigger: "axis"
      },
      xAxis: [
        // type: "category",
        // data: []
        {
            type: 'category',
            boundaryGap: true,
            // data: (function (){
            //     var now = new Date();
            //     var res = [];
            //     var len = 10;
            //     while (len--) {
            //         res.unshift(now.toLocaleTimeString().replace(/^\D*/,''));
            //         now = new Date(now - 2000);
            //     }
            //     return res;
            // })()
            data:['一线','二线','三线']
        },
      ],
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
    },
      yAxis: [
        {
        type: "value",
        boundaryGap: [0.2, 0.2],
        max: 8000,
        min: 0,
        },
        {
        type: "value",
        boundaryGap: [0.2, 0.2],
        max:100,
        min: 0,
        },
      ],
      series: [
        {
          name: '2011年',
          type: 'bar',
          data: [400, 300, 100, 500, 600, 700],
        },
        {
          name: '2011年',
          type: 'line',
          data: [40, 30, 10, 50, 60, 70],
        },
      ]
      });
    },
  }
};



